import { Slot } from '@radix-ui/react-slot'
import { cva, type VariantProps } from 'class-variance-authority'
import * as React from 'react'

import { LoadingIcon } from '@/assets/icons/loading'
import { cn } from '@/lib/utils'

const buttonVariants = cva(
	'relative inline-flex items-center justify-center gap-2 whitespace-nowrap rounded-md text-sm font-medium ring-offset-background transition-colors focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring focus-visible:ring-offset-2 disabled:pointer-events-none disabled:opacity-50 [&_svg]:pointer-events-none [&_svg]:size-4 [&_svg]:shrink-0 [&_#loading-icon>path]:stroke-primary',
	{
		variants: {
			variant: {
				default: 'bg-primary text-primary-foreground hover:bg-primary/90 [&_#loading-icon>path]:stroke-white',
				destructive: 'bg-destructive text-destructive-foreground hover:bg-destructive/90 [&_#loading-icon>path]:stroke-white',
				outline: 'text-primary border border-primary bg-background hover:bg-accent hover:text-primary',
				secondary: 'bg-secondary text-secondary-foreground hover:bg-secondary/80',
				ghost: 'hover:bg-accent hover:text-accent-foreground',
				link: 'text-primary underline-offset-4 hover:underline',
			},
			size: {
				default: 'h-11 px-4 py-2',
				sm: 'h-9 rounded-md px-3',
				lg: 'h-11 rounded-md px-8',
				icon: 'h-10 w-10',
			},
		},
		defaultVariants: {
			variant: 'default',
			size: 'default',
		},
	}
)

export interface ButtonProps extends React.ButtonHTMLAttributes<HTMLButtonElement>, VariantProps<typeof buttonVariants> {
	asChild?: boolean
	loading?: boolean
}

const Button = React.forwardRef<HTMLButtonElement, ButtonProps>(({ className, variant, size, asChild = false, children, loading, ...props }, ref) => {
	const Comp = asChild ? Slot : 'button'
	return (
		<Comp className={cn(buttonVariants({ variant, size, className }), loading && 'text-transparent [&_svg]:invisible')} ref={ref} disabled={loading} {...props}>
			{loading && <LoadingIcon className='!visible !size-10 absolute top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2' />}
			{children}
		</Comp>
	)
})
Button.displayName = 'Button'

export { Button, buttonVariants }
